import { myCarts,checkCart,deleteCart,updateCart,addCart } from '@/api/cart'

const state = {
    carts: {},
}


const mutations = {
    setCarts(state, carts) {
        state.carts = carts;
    }
}


const actions = {
    myCarts({ commit }) {
        return new Promise((resolve, reject) => {
            myCarts().then(res => {
                commit('setCarts', res.data.result)
                resolve(res)
            }).catch(error => {
                reject(error)
            })
        })
    },
    addCart({ commit },{skuCode,num}) {
        return new Promise((resolve, reject) => {
            addCart("",skuCode,num).then(res => {
                resolve(res)
            }).catch(error => {
                reject(error)
            })
        })
    },
    checkCart({ commit }, {id}) {
        return new Promise((resolve, reject) => {
            checkCart(id).then(res => {
                resolve(res)
            }).catch(error => {
                reject(error)
            })
        })
    },
    deleteCart({ commit }, {id}) {
        return new Promise((resolve, reject) => {
            deleteCart(id).then(res => {
                resolve()
            }).catch(error => {
                reject(error)
            })
        })
    },
    updateCart({ commit }, {id,num,skuCode}) {
        return new Promise((resolve, reject) => {
            updateCart(id,num,skuCode).then(res => {
                resolve()
            }).catch(error => {
                reject(error)
            })
        })
    }
}

export default {
    state,
    mutations,
    actions
}



