import request from '@/common/js/fetch'

export function indexData(latitude,longitude) {
  const data = {
    longitude:longitude,
    latitude:latitude
  }
  return request({
    url: '/index/baseData',
    method: 'get',
    params:data
  })
}

export function productData() {

  return request({
    url: '/index/productData',
    method: 'get'
  })
}



