<template>
  <div id="app">
    <router-view v-on:showDrawer="showDrawer"  />
    <drawer ref="appDrawer" />
  </div>
</template>

<script>
import { setTimeout } from "timers";
export default {
  name: "App",
  created() {

    if (this.$store.getters.token) {
      this.$store.dispatch("myWishes").then(res => {});

      this.$store.dispatch("userInfo").then(r => {
      });
    }
  },
  methods: {
    showDrawer(){
      console.log(1)
      this.$refs.appDrawer.showDrawer();
    }
  },
  mounted() {
    let isLoad = false;
    setTimeout(() => {
      if (isLoad) {
        return false;
      }
      this.$store.dispatch("indexData", { lon: 1, lan: 1 }).then(r => {});
    }, 2000);
    this.$store.dispatch("getUserInfo").then(res => {
      isLoad = true;
      this.$store
        .dispatch("indexData", { lon: res.longitude, lan: res.latitude })
        .then(r => {});
    });
  },
  watch: {
    "$store.getters.token": function(val) {
      if (val) {
        this.$store.dispatch("myWishes").then(res => {});

        this.$store.dispatch("userInfo").then(r => {
          if(!r.superiorId){
            this.$router.push('fillInvitation');
          }
            console.log(r);
        });
      }
    },
    "$route.matched":function(val){
      //  console.log(val);
    }
  }
};
</script>

<style lang="scss">
@import "./common/css/common.scss";
</style>



