export default {
    getCategoryOne: config => {
      let arr=['Dress','Children','Flower','Home textiles','Jewelry ornaments','Beauty care','Consumer electronics','Stationery'];
      let back=[' &#xe645;','&#xe64a;','&#xe646;','&#xe647;','&#xe649;','&#xe64c;','&#xe66a;','&#xe64d;'];
      let res=[];
      for (let index = 0; index < arr.length; index++) {
          const element = arr[index];
         res.push({
            "categoryName": element,
            "categoryCode":"0"+(index+1),
            "img": require('@/common/images/icon_manue_0'+(index+2)+'.png'),
            'back':back[index]
         });
      }

      return {code:200000,result:res};
  }
}


